<template>
  <div class="layout">
    <Layout>
      <Header>
        <div class="header-left">
          <div class="layout-logo"><img src="../../assets/img/logo.png" /></div>
          <div class="openShrink">
            <Icon type="md-apps" size="30" @click="toggleCollapse" />
          </div>
        </div>
        <div class="handle">
          <user />
        </div>
      </Header>
      <Layout>
        <Sider
          hide-trigger
          :style="{ background: '#fff' }"
          ref="side"
          collapsible
          :collapsed-width="78"
          v-model="isCollapsed"
        >
          <Menu
            :class="menuitemClasses"
            accordion
            theme="dark"
            width="auto"
            @on-select="handleMenuItem"
            @on-open-change="handleMenuOpen"
            :active-name="activeName"
            :open-names="openNames"
            ref="menu"
          >
            <div v-for="(item, index) in menuList" :key="index">
              <!-- 二级菜单 -->
              <div v-if="item.children && item.children.length > 0">
                <Submenu :name="item.name">
                  <div slot="title">
                    <Icon :type="item.icon"></Icon>
                    <span>{{ item.name }}</span>
                  </div>
                  <div v-for="(sub, index2) in item.children" :key="index2">
                    <MenuItem :name="sub.path" :to="sub.path">{{
                      sub.name
                    }}</MenuItem>
                  </div>
                </Submenu>
              </div>
              <!-- 一级菜单 -->
              <div v-else>
                <MenuItem :name="item.path" :to="item.path">
                  <Icon type="ios-navigate"></Icon>
                  <span>{{ item.name }}</span>
                </MenuItem>
              </div>
            </div>
          </Menu>
        </Sider>
        <Layout :style="{ padding: '0 24px 24px', marginTop: '15px' }">
          <div class="tags-nav">
            <tags-nav></tags-nav>
          </div>
          <Content :style="{ padding: '24px', background: '#fff' }">
            <router-view></router-view>
          </Content>
        </Layout>
      </Layout>
    </Layout>
  </div>
</template>
<script>
import User from './components/user/user.vue'
import tagsNav from './components/tags-nav/tags-nav.vue'
export default {
  components: {
    User,
    tagsNav
  },
  data() {
    return {
      //折叠菜单
      isCollapsed: false,
      //菜单数据
      menuList: [],
      //菜单高亮
      activeName: '',
      //展开的二级菜单
      openNames: []
    }
  },
  computed: {
    rotateIcon() {
      return ['menu-icon', this.isCollapsed ? 'rotate-icon' : '']
    },
    menuitemClasses() {
      return ['menu-item', this.isCollapsed ? 'collapsed-menu' : '']
    }
  },
  mounted() {
    this.getMenuListInfo()
  },
  methods: {
    //获取菜单数据
    getMenuListInfo() {
      const menuList = [
        {
          name: '首页',
          path: '/welcome'
        },
        {
          name: '用户管理',
          icon: 'ios-contact-outline',
          children: [
            {
              parentName: '用户管理',
              name: '用户列表',
              path: '/users'
            }
          ]
        },
        {
          name: '系统管理',
          icon: 'ios-keypad',
          children: [
            {
              parentName: '系统管理',
              name: '角色列表',
              path: '/roles'
            },
            {
              parentName: '系统管理',
              name: '权限列表',
              path: '/rights'
            }
          ]
        }
      ]
      this.menuList = menuList
      this.setActiveNameAndOpenNames()
    },
    //折叠菜单
    toggleCollapse() {
      this.$refs.side.toggleCollapse()
    },
    //点击菜单
    handleMenuItem(name) {
      this.isCollapsed = false
      this.activeName = name
    },
    //展开菜单
    handleMenuOpen(names) {
      this.isCollapsed = false
      this.openNames = names
    },
    /**
     * 1.判断route中如果有path，则把route.path存到session中的activeName里
     *
     * 2.拿到session中的activeName
     *
     * 3.因为menu的数据有一级菜单或者二级菜单，循环左侧菜单的menu数据，如果menuList的一级菜单中的path和当前activeName
     * 相同，就会返回true,（第4步会用到），再循环menuList中的二级菜单，如果menuList的二级菜单中的path和当前activeName
     * 相同，就会返回true（第4步会用到），当然一级菜单和二级菜单中不可能同时存在和activeName相同的值，他们的path路径都是独立的，
     * 就是说他们两个不可能同时返回true
     *
     * 4.判断activeName存在并且一级菜单中存在activeName相同的path,或者二级菜单中存在相同的path
     * 循环menuList中一级菜单的path和activeName相同的话,直接赋值给activeName，让左侧menu对应的菜单高亮。。。
     * 如果菜单中没有二级菜单，直接返回，不操作判断二级菜单的代码。。。
     * 循环二级菜单存在activeName相同的path,直接赋值给activeName，这个时候就可以拿到他的二级菜单中的的parentName,
     * 直接赋值给openNames，他的父级就会展开，并且子集也会高亮
     *
     * 还有一个可能就是（动态路由跳转带参数），通过一个id跳转到一个详情页面，通过id获取对应的数据，
     * menuList中并不会存在该页面的路由，当切换右侧tag的时候，他的 activeName 并不会和menuList中
     * 数据的path相同，这个时候就需要把activeName 赋值成 "" ，从而让左侧menu不选中
     */
    setActiveNameAndOpenNames(route) {
      // 1
      if (route) sessionStorage.setItem('activeName', route.path)
      // 2
      const activeName = sessionStorage.getItem('activeName') || '/welcome'
      // 3
      let temporary = this.menuList.some(item => item.path === activeName)
      let temporarySub = false
      try {
        this.menuList.forEach(item => {
          if (!item.children) return
          temporarySub = item.children.some(sub => {
            return sub.path === activeName
          })
          if (temporarySub) {
            throw new Error(true)
          }
        })
      } catch (e) {
        console.log(e)
      }
      //4
      if ((activeName && temporary) || (activeName && temporarySub)) {
        this.menuList.forEach(item => {
          if (item.path === activeName) {
            this.activeName = activeName
            this.openNames = []
          }
          if (!item.children) return
          item.children.forEach(sub => {
            if (sub.path === activeName) {
              this.activeName = activeName
              this.openNames = [sub.parentName]
            }
          })
        })
      } else {
        this.activeName = ''
        this.openNames = []
      }
    }
  },
  watch: {
    $route(newRoute) {
      this.setActiveNameAndOpenNames(newRoute)
    },
    activeName() {
      this.$nextTick(() => {
        this.$refs.menu.updateActiveName()
      })
    },
    openNames() {
      this.$nextTick(() => {
        this.$refs.menu.updateOpened()
      })
    },
    isCollapsed(value) {
      if (value) {
        this.openNames = []
      } else {
        this.setActiveNameAndOpenNames()
      }
    }
  }
}
</script>
