<template>
  <Dropdown @on-click="handleClick">
    <Avatar icon="ios-person" size="large">下拉菜单</Avatar>
    <DropdownMenu slot="list">
      <DropdownItem name="logout">退出登录</DropdownItem>
    </DropdownMenu>
  </Dropdown>
</template>
<script>
export default {
  methods: {
    logout() {
      sessionStorage.clear()
      this.$router.push('/login')
    },
    handleClick(name) {
      switch (name) {
        case 'logout':
          this.logout()
          break
      }
    }
  }
}
</script>
