<template>
  <div class="tags-nav">
    <Tag
      type="dot"
      :closable="item.name !== 'welcome'"
      checkable
      v-for="(item, index) in tagNavListInfo"
      :key="index"
      :color="isActive(item) ? 'primary' : 'default'"
      @on-close="handleClose(item)"
      @click.native="turnToPage(item)"
      >{{ item.meta.title }}</Tag
    >
  </div>
</template>
<script>
export default {
  data() {
    return {}
  },
  computed: {
    tagNavListInfo() {
      return this.$store.state.tagNavList
    }
  },
  methods: {
    //判断页面的路由是否为当前路由
    isActive(route) {
      return route.path === this.$route.path
    },
    //关闭标签
    handleClose(route) {
      this.$store.dispatch('deleteTag', route).then(tagNavList => {
        let route = tagNavList.slice(-1)[0]
        if (route) {
          let { name, params, query, meta, path } = route
          this.$router.push({
            name,
            params,
            query,
            meta,
            path
          })
        } else {
          this.$router.push('/')
        }
      })
    },
    //切换路由
    turnToPage(route) {
      let { name, params, query, meta, path } = route
      this.$router.push({
        name,
        params,
        query,
        meta,
        path
      })
    }
  }
}
</script>
<style lang="less" scoped>
.tags-nav {
  margin-bottom: 10px;
  overflow-y: auto;
  white-space: nowrap;
}
// 美化滚动条样式
::-webkit-scrollbar-track-piece {
  border-radius: 0;
}
::-webkit-scrollbar {
  width: 5px;
  height: 10px;
}
::-webkit-scrollbar-thumb {
  height: 50px;
  background-color: #b8b8b8;
  border-radius: 6px;
  outline-offset: -2px;
  -moz-opacity: 0.5;
  -khtml-opacity: 0.5;
  opacity: 0.5;
}
::-webkit-scrollbar-thumb:hover {
  height: 50px;
  background-color: #878987;
  border-radius: 6px;
}
</style>
